.project-heading,
.project-heading:before {
  background-color: var(--color-3);
  color: var(--color-5) !important;
  text-align: center;
  padding: 0.5rem 0;
  margin-top: 3rem !important;
  border-radius: 7rem 0;
  width: 100%;
}

.project-heading:before {
  margin-top: 0 !important;
}

.card {
  position: relative;
  width: 280px;
  height: 400px;
  margin: 30px;
  box-shadow: 20px 20px 50px rgba(0, 0, 0, 0.5);
  border-radius: 15px;
  background: rgba(255, 255, 255, 0.1);
  overflow: hidden;
  display: flex;
  justify-content: center;
  align-items: center;
  backdrop-filter: blur(5px);
}
.card .content {
  padding: 20px;
  text-align: center;
  transition: 0.5s;
}
